import React, { useState } from 'react'
import styled from 'styled-components';
import { ThemeProps } from '../../@types/theme';
import ProjectSideBar from '../../components/Sidebar/ProjectSidebar';
import ProjectList from './ProjectList';

const StyledProjectWrapper = styled.div`
  display: flex;
  width: 100%;
  height: 100%;
`;

const StyledContentWrapper = styled.div`
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  height: 100%;
`;

const StyledBlockHeader = styled.div`
  height: 40px;
  width: 100%;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  padding: 0 15px;
  border-bottom: 1px solid ${({ theme }: { theme: ThemeProps }) => theme.colors.lines};
`

const StyledBlockTitle = styled.span`
  height: 100%;
  display: flex;
  padding-right: 30px;
  align-items: center;
  max-width: 100%;
  min-width: 150px;
`

const StyledBlockCloseIcon = styled.div`
  height: 100%;
  display: flex;
  padding: 0 10px;
  align-items: center;
  font-size: 20px;
  border-right: 1px solid ${({ theme }: { theme: ThemeProps }) => theme.colors.lines};
`

const StyledContent = styled.div`
  flex-grow: 1;
  overflow: hidden;
`
export interface IStacksData {
  name: string;
  icon: string;
  isChecked: boolean;
}

const Projects = () => {

  const stacksData: IStacksData[] = [
    {
      name: "React",
      icon: "ri-reactjs-fill",
      isChecked: true,
    },
    {
      name: "HTML",
      icon: "ri-html5-fill",
      isChecked: false,
    },
    {
      name: "CSS",
      icon: "ri-css3-fill",
      isChecked: false,
    },
    {
      name: "Angular",
      icon: "ri-angularjs-fill",
      isChecked: true,
    },
  ]

  const [stacks, setStacks] = useState<IStacksData[]>(stacksData)

  return (
    <StyledProjectWrapper>
      <ProjectSideBar stacks={stacks} setStacks={setStacks} />
      <StyledContentWrapper>
        <StyledBlockHeader>
          <StyledBlockTitle>
            {stacks.filter((stack) => stack.isChecked).map((stack) => stack.name).join('; ') || 'All Stacks'}
          </StyledBlockTitle>
          <StyledBlockCloseIcon>
            <i className="ri-close-fill"></i>
          </StyledBlockCloseIcon>
        </StyledBlockHeader>
        <StyledContent>
          <ProjectList />
        </StyledContent>
      </StyledContentWrapper>
    </StyledProjectWrapper >
  )
}

export default Projects