import styled from 'styled-components';
import { ThemeProps } from '../../../@types/theme';
import DefaultProjectImage from "../../../assets/image/900.png";

const StyledFolderModalWrapper = styled.div`
  display: block;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
`

const StyledModalContentWrapper = styled.div`
  position: absolute;
  -webkit-clip-path: polygon(6% 40%, 0 34%, 0 0, 100% 0, 100% 100%, 6% 100%);
  clip-path: polygon(6% 40%, 0 34%, 0 0, 100% 0, 100% 100%, 6% 100%);
  background-color: ${({ theme }: { theme: ThemeProps }) => theme.colors.secondary1};
  border-radius: 6px;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  width: calc(100% - 60px);
  height: calc(100% - 60px);
  overflow: hidden;
`

const StyledModalContent = styled.div`
  position: absolute;
  -webkit-clip-path: polygon(6% 40%, 0 34%, 0 0, 100% 0, 100% 100%, 6% 100%);
  clip-path: polygon(6% 40%, 0 34%, 0 0, 100% 0, 100% 100%, 6% 100%);
  background-color: ${({ theme }: { theme: ThemeProps }) => theme.colors.primary1};
  color: ${({ theme }: { theme: ThemeProps }) => theme.colors.secondary1};
  border-radius: 6px;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  padding: 10px;
  width: calc(100% - 1px);
  height: calc(100% - 1px);
  display: flex;
  flex-direction: column;
  overflow: hidden;
`

const StyledCloseIcon = styled.div`
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 32px;
  color: ${({ theme }: { theme: ThemeProps }) => theme.colors.secondary1};
  cursor: pointer;
  height: 32px;
  width: 34px;
  top: 8px;
  right: 8px;

  &:hover{
    color: ${({ theme }: { theme: ThemeProps }) => theme.colors.secondary4};
  }
`

const StyledModalHeader = styled.h3`
  padding-left: calc(6% + 12px);
  color: ${({ theme }: { theme: ThemeProps }) => theme.colors.accent2};
`

const StyledModalSubHeader = styled.p`
  padding-left: calc(6% + 12px);
  padding-bottom: 20px;
  color: ${({ theme }: { theme: ThemeProps }) => theme.colors.secondary1};
`

const StyledModalBody = styled.div`
  padding-left: 6%;
  display: flex;
  overflow: hidden;
  width: 100%;
  height: 100%;
`

const StyledModalLeft = styled.div`
  padding: 0 16px 0 6px;
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  border-right: 1px solid ${({ theme }: { theme: ThemeProps }) => theme.colors.lines};
  overflow-y: auto;
`

const StyledModalRight = styled.div`
  padding: 0 6px 0 16px;
  width: 50%;
  height: 100%;
  overflow-y: auto;
`

const StyledProjectImageWrapper = styled.div`
  border: 2px dashed ${({ theme }: { theme: ThemeProps }) => theme.colors.accent4};
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 400px;
  padding: 4px;
`

const StyledProjecImg = styled.img`
  border-radius: 6px;
  width: 100%;
  height: 100%;
  object-fit: contain;
`

const StyledImageIndexWrapper = styled.div`
  width: 100%;
  height: 140px;
  display: flex;
  justify-content: space-evenly;
`

const StyledImageContainer = styled.div`
  border-radius: 6px;
  height: 100%;
  padding: 4px;
  opacity: 0.6;
  cursor: pointer;
  transition: .3s;
  &.active{
    border: 1px solid ${({ theme }: { theme: ThemeProps }) => theme.colors.accent2};
    opacity: 1;
  }
  &:hover{
    opacity: 1;
  }
`

const StyledImageIndex = styled.img`
  border-radius: 6px;
  height: 100%;
  object-fit: contain;
`

const DescriptionWrap = styled.div`
  padding-bottom: 20px;
`
const Description = styled.div`
  flex-direction: column;
  gap: 20px;
  p:not(:last-child) {
    padding-bottom: 16px;
  }
`

const TechWrap = styled.div`
  padding-bottom: 20px;
`

const ActionWrap = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
  a {
    color: ${({ theme }: { theme: ThemeProps }) => theme.colors.accent3};
    &:hover,:visited,:active{
      color: ${({ theme }: { theme: ThemeProps }) => theme.colors.accent3};
    }
  }
`

const StyledInfoHeader = styled.h3`
  font-size: 20px;
  color: ${({ theme }: { theme: ThemeProps }) => theme.colors.secondary3};
  padding-bottom: 6px;
`

const StyledPillsWraper = styled.div`
  display: flex;
  gap: 8px;
`

const StyledTechPills = styled.span`
  border-radius: 16px;
  background-color: ${({ theme }: { theme: ThemeProps }) => theme.colors.secondary2};
  color: ${({ theme }: { theme: ThemeProps }) => theme.colors.btnPrimaryText};
  padding: 4px 12px;
`

interface IFolderModal {
  isOpen: boolean;
  toggle: () => void;
}

const FolderModal = (props: IFolderModal) => {
  const { isOpen = false, toggle } = props;

  return isOpen ? (
    <StyledFolderModalWrapper>
      <StyledModalContentWrapper>
        <StyledModalContent>
          <StyledCloseIcon onClick={toggle}>
            <i className='ri-close-fill'></i>
          </StyledCloseIcon>
          <StyledModalHeader>
            The Project Title
          </StyledModalHeader>
          <StyledModalSubHeader>
            The Project Title
          </StyledModalSubHeader>
          <StyledModalBody>
            <StyledModalLeft>
              <StyledProjectImageWrapper>
                <StyledProjecImg src={DefaultProjectImage} />
              </StyledProjectImageWrapper>
              <StyledImageIndexWrapper>
                {/* // only 3 images */}
                <StyledImageContainer className='active'>
                  <StyledImageIndex src={DefaultProjectImage} />
                </StyledImageContainer>
                <StyledImageContainer className=''>
                  <StyledImageIndex src={DefaultProjectImage} />
                </StyledImageContainer>
                <StyledImageContainer className=''>
                  <StyledImageIndex src={DefaultProjectImage} />
                </StyledImageContainer>
              </StyledImageIndexWrapper>
            </StyledModalLeft>
            <StyledModalRight>
              <DescriptionWrap>
                <StyledInfoHeader>Project Description</StyledInfoHeader>
                <Description>
                  <p>
                    Lorem ipsum dolor, sit amet consectetur adipisicing elit. Obcaecati cum voluptas quasi ex modi temporibus omnis consequuntur nobis totam fuga aut, ut deserunt aspernatur quis perferendis facilis voluptatibus corporis officia eaque ipsam esse nemo architecto eum repellat. Fugit, eaque vitae?
                  </p>
                  <p>
                    Lorem ipsum dolor, sit amet consectetur adipisicing elit. Obcaecati cum voluptas quasi ex modi temporibus omnis consequuntur nobis totam fuga aut, ut deserunt aspernatur quis perferendis facilis voluptatibus corporis officia eaque ipsam esse nemo architecto eum repellat. Fugit, eaque vitae?
                  </p>
                  <p>
                    Lorem ipsum dolor, sit amet consectetur adipisicing elit.
                  </p>
                </Description>
              </DescriptionWrap>
              <TechWrap>
                <StyledInfoHeader>Technology Used</StyledInfoHeader>
                <StyledPillsWraper>
                  <StyledTechPills>React</StyledTechPills>
                  <StyledTechPills>HTML</StyledTechPills>
                  <StyledTechPills>CSS</StyledTechPills>
                  <StyledTechPills>Styled Component</StyledTechPills>
                  <StyledTechPills>React Query</StyledTechPills>
                </StyledPillsWraper>

              </TechWrap>
              <ActionWrap>
                <div>
                  <StyledInfoHeader>Code</StyledInfoHeader>
                  <a href="http://#">http://hereisthe.code</a>
                </div>
                <div>
                  <StyledInfoHeader>Demo</StyledInfoHeader>
                  <a href="http://#">http://hereisthe.demo</a>
                </div>
              </ActionWrap>
            </StyledModalRight>
          </StyledModalBody>
        </StyledModalContent>
      </StyledModalContentWrapper>
    </StyledFolderModalWrapper>
  ) : null
}

export default FolderModal