import React, { useState } from 'react'
import styled from 'styled-components'
import { ThemeProps } from '../../../@types/theme'
import DefaultProjectImage from "../../../assets/image/900.png";
import FolderModal from './FolderModal';

const StyledProjectItem = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  width: 350px;
`

const StyledCardWrapper = styled.div`
  display: flex;
  position: relative;
  width: 350px;
  height: 280px;
  box-sizing: border-box;
  border-radius: 6px;
  background-color: ${({ theme }: { theme: ThemeProps }) => theme.colors.secondary1};
  -webkit-clip-path: polygon(34% 0, 40% 10%, 100% 10%, 100% 100%, 0 100%, 0 0);
  clip-path: polygon(34% 0, 40% 10%, 100% 10%, 100% 100%, 0 100%, 0 0);
`

const StyledProjecCard = styled.div`
  display: flex;
  position: absolute;
  top: 1px;
  left: 1px; 
  width: 348px; 
  height: 278px; 
  -webkit-clip-path: polygon(34% 0, 40% 10%, 100% 10%, 100% 100%, 0 100%, 0 0);
  clip-path: polygon(34% 0, 40% 10%, 100% 10%, 100% 100%, 0 100%, 0 0);

  .hide {
    visibility: hidden;
  }

  &:hover{
    .hide {
      visibility: visible;
      background-color: rgba(4, 33, 50, 0.568);
      border-radius: 8px;
      transition: 0.4s;
    }
  } 
`
const StyledProjecTitle = styled.h4`
  position: absolute;
  top: 2px;
  right: 2px;
  color: ${({ theme }: { theme: ThemeProps }) => theme.colors.accent4};
  margin-bottom: 14px;
`

const StyledProjecImg = styled.img`
  object-fit: cover;
  border-radius: 6px;
  width: 100%;
`

const StyledTextIcon = styled.div`
  position: absolute;
  display: grid;
  font-size: 20px;
  font-weight: 500;
  place-items: center;
  border-radius: 4px;
  color: ${({ theme }: { theme: ThemeProps }) => theme.colors.btnPrimaryText};
  top: 18px;
  left: 18px;
  height: 30px;
  width: 30px;
  background-color: ${({ theme }: { theme: ThemeProps }) => theme.colors.accent2};
`

const StyledButtonWrapper = styled.div`
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  gap: 16px;
  padding-bottom: 10px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  &:hover{
    visibility: visible;
  }
`

const StyledButton = styled.button`
  width: 150px;
  outline: 1px solid ${({ theme }: { theme: ThemeProps }) => theme.colors.accent2};
  border-radius: 4px;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  background-color: transparent;
  color: ${({ theme }: { theme: ThemeProps }) => theme.colors.accent2};

  &:hover{
    color: ${({ theme }: { theme: ThemeProps }) => theme.colors.btnPrimaryText};
    background-color: ${({ theme }: { theme: ThemeProps }) => theme.colors.accent2};
  }
`

const ProjectItem = () => {
  const [isModalOpen, setIsModalOpen] = useState<boolean>(false);
  const toggleModal = () => setIsModalOpen(!isModalOpen);

  return (
    <>
      <StyledProjectItem>
        <StyledProjecTitle>
          React
        </StyledProjecTitle>
        <StyledCardWrapper>
          <StyledProjecCard>
            <StyledProjecImg src={DefaultProjectImage} />
            <StyledTextIcon>
              <i className='ri-reactjs-fill'></i>
            </StyledTextIcon>
            <StyledButtonWrapper className='hide'>
              <StyledButton onClick={toggleModal}>
                View Detail
              </StyledButton>
              <StyledButton>
                Demo
              </StyledButton>
              <StyledButton>
                Code
              </StyledButton>
            </StyledButtonWrapper>
          </StyledProjecCard>
        </StyledCardWrapper>
      </StyledProjectItem>
      <FolderModal isOpen={isModalOpen} toggle={toggleModal} />
    </>
  )
}

export default ProjectItem