import React from 'react'
import styled from 'styled-components';
import { ThemeProps } from '../../../@types/theme';

const StyledSideNavWrapper = styled.div`
  display: flex;
  flex-direction: column;
  border-right: 1px solid ${({ theme }: { theme: ThemeProps }) => theme.colors.lines};
  width: 240px;
  height: 100%;
  flex-shrink: 0;
  overflow-y: auto;
`;

const StyledSideNabTitle = styled.span`
  color: ${({ theme }: { theme: ThemeProps }) => theme.colors.secondary4};
  display: flex;
  gap: 4px;
  align-items: center;
`

const StyledSideNabIcon = styled.span`
  font-size: 20px;
`

const StyledContactInfoWrapper = styled.div`
  display: flex;
  flex-direction: column;
  padding-top: 10px;
  padding-bottom: 20px;
  border-bottom: 1px solid ${({ theme }: { theme: ThemeProps }) => theme.colors.lines};
`

const StyledContactLinksWrapper = styled.div`
  display: flex;
  flex-direction: column;
  padding: 10px 0;
  
`

const StyledBlockHeader = styled.div`
  height: 40px;
  display: flex;
  align-items: center;
  padding: 0 15px;
  border-bottom: 1px solid ${({ theme }: { theme: ThemeProps }) => theme.colors.lines};
`

const StyledContactInfoItem = styled.span`
  display: flex;
  align-items: center;
  padding: 4px 15px;
`

const StyledContactLinkItem = styled.a`
  display: flex;
  align-items: center;
  padding: 4px 15px;
  text-decoration: none;
  color: ${({ theme }: { theme: ThemeProps }) => theme.colors.secondary1};
  &:visited, &:active{
    color: ${({ theme }: { theme: ThemeProps }) => theme.colors.socialMedia};
  }
  &:hover{
    color: ${({ theme }: { theme: ThemeProps }) => theme.colors.socialMediaHover};
  }
`

export const StyledInfoIcon = styled.span`
  display: flex;
  align-items: center;
  font-size: 20px;
  margin-right: 10px;
`

const ContactSideBar = () => {
  return (
    <>
      <StyledSideNavWrapper>
        <StyledBlockHeader>
          <StyledSideNabTitle>
            <StyledSideNabIcon>
              <i className="ri-arrow-down-s-fill"></i>
            </StyledSideNabIcon>
            contacts
          </StyledSideNabTitle>
        </StyledBlockHeader>
        <StyledContactInfoWrapper>
          <StyledContactInfoItem>
            <StyledInfoIcon>
              <i className="ri-mail-fill"></i>
            </StyledInfoIcon>
            bhattaraibikash55
          </StyledContactInfoItem>
          <StyledContactInfoItem>
            <StyledInfoIcon>
              <i className="ri-phone-fill"></i>
            </StyledInfoIcon>
            +977 9849940443
          </StyledContactInfoItem>
        </StyledContactInfoWrapper>
        <StyledBlockHeader>
          <StyledSideNabTitle>
            <StyledSideNabIcon>
              <i className="ri-arrow-down-s-fill"></i>
            </StyledSideNabIcon>
            find-me-also-in
          </StyledSideNabTitle>
        </StyledBlockHeader>
        <StyledContactLinksWrapper>
          <StyledContactLinkItem href='https://www.facebook.com/bethebikash' target="_blank">
            <StyledInfoIcon>
              <i className="ri-external-link-fill"></i>
            </StyledInfoIcon>
            Facebook
          </StyledContactLinkItem>
          <StyledContactLinkItem href='https://www.instagram.com/bethebikash' target="_blank">
            <StyledInfoIcon>
              <i className="ri-external-link-fill"></i>
            </StyledInfoIcon>
            Instagram
          </StyledContactLinkItem>
          <StyledContactLinkItem href='https://www.twitter.com/bethebikash' target="_blank">
            <StyledInfoIcon>
              <i className="ri-external-link-fill"></i>
            </StyledInfoIcon>
            Twitter
          </StyledContactLinkItem>
        </StyledContactLinksWrapper>
      </StyledSideNavWrapper>
    </>
  )
}

export default ContactSideBar