import React from 'react'
import styled from 'styled-components'
import ProjectItem from './ProjectItem'

const StyledProjectList = styled.div`
  padding: 20px;
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  height: 100%;
  width: 100%;
  justify-content: space-around;
  overflow: auto;
`

const ProjectList = () => {
  return (
    <StyledProjectList>
      <ProjectItem />
      <ProjectItem />
      <ProjectItem />
      <ProjectItem />
      <ProjectItem />
    </StyledProjectList>
  )
}

export default ProjectList